body.darkMode {
  background-color: rgba(0, 0, 0, 1);
  color: #fff;

  &::before {
    content: "";
    opacity: 0.3;
    filter: invert(1);
  }

  .close-btn {
    color: #fff;
  }

  .modal {
    background-color: rgb(6, 6, 6) !important;
    color: #fff !important;

    a {
      font-weight: bold;
    }
  }

  button:not(.close-btn, .list-tab-button, .unsubcribe-btn) {
    // background-color: rgb(6, 6, 6) !important;
    background-color: #ffd700;
    color: #000 !important;
    img {
      filter: contrast(140%);
    }

    &:hover {
      background-color: #ffde24 !important;
    }
  }

  .post-item {
    background-color: rgb(6, 6, 6) !important;
    color: #fff !important;
    border: none;

    .head {
      background-color: rgba(162, 162, 162, 0.1);

      .unsubcribe-btn {
        color: #fff !important;
        opacity: 0.6;
      }
    }

    .footer .footer-bottom .post-source {
      a {
        color: #ffd700;
      }
    }
  }

  .settings {
    color: #fff !important;
  }

  .back-button {
    color: #000;
    background-color: #ffd700;
  }

  .channels-grid .channel {
    border-color: rgba(rgb(199, 199, 199), 0.1);
  }

  .toTopButton {
    background-color: #0057b7;

    img {
      filter: brightness(-100%) invert(1);
    }
  }

  .list-tab-button {
    background-color: #000;
    color: #fff !important;

    &.active {
      background-color: #ffd700;
      color: #000 !important;
    }
  }

  .footer-desc {
    color: #858585 !important;
  }

  .post-item-wrapper {
    background-color: rgba(6, 6, 6, .1) !important;
  }

  .setting-select,
  .q-menu {
    filter: invert(1);

    .q-item {
      color: #000 !important;
    }
  }
}
