// app global css in SCSS form

@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

:root {
  --mainColor: #ff114e;
  --zIndex: 10;
}

html,
body,
#app {
  height: 100%;
}

#app > div {
  height: 100%;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Inter", "Open Sans", sans-serif;
  letter-spacing: 0.1px;
}

a:hover,
button:hover {
  cursor: pointer;
  filter: brightness(95%);
}

p {
  margin: 0;
}

a:active,
button:active {
  filter: brightness(90%);
}

i {
  font-style: normal !important;
  font-weight: bold;
  padding: 0 2px;
}

body {
  background: rgb(211, 211, 211, 0.5);
  min-width: 320px;
  overflow-x: hidden !important;

  &::before {
    content: "";
    position: fixed;
    background-image: url("@/assets/img/blur.webp");
    background-size: 100% 100%;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0.9;
    transition: filter .3s linear; //xm
  }
}

.container-fluid {
  padding: 0 6px;
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 99999;
  background-color: #0048aa;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

@keyframes fly {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0);
  }
}

.loader-image {
  width: 50px;
  animation: fly 1s linear infinite;
}

a {
  text-decoration: none;
  color: #000;
}

li {
  list-style: none;
}

.emoji {
  margin: 0 2px -4px;
  width: 20px;
  height: 20px;
  display: inline-block;
  background-position: center 50%;
  background-size: 100%;

  b {
    display: none;
  }
}

pre {
  white-space: pre-wrap;
}

iframe {
  border: none;
}

.back-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f5;
  border-radius: 5px;
  color: #000;
  // height: 30px;
  // padding: 10px;
  width: 30px;
  height: 30px;
  font-size: 30px;

  @media (max-width: 700px) {
    // margin-left: 6px;
  }
}

.toTopButton {
  position: fixed;
  bottom: 20px;
  right: 14px;
  width: 40px;
  height: 40px;
  background-color: #e1eaf3;
  border-radius: 100%;
  z-index: 1;
  opacity: 0;
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: #fff;
  cursor: pointer;
  transform-origin: right center;
  transform: scale(0.9) translateY(10px);
  transition: all 0.3s ease;

  img {
    width: 40%;
    // filter: invert(1);
  }

  .new-posts-count {
    position: absolute;
    top: 4px;
    right: 4px;
    background-color: #ffd700;
    border-radius: 100px;
    padding: 2px 4px;
    min-width: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    color: #000;
    transform: translate(50%, -50%);
  }

  &.active {
    transform: scale(1) translateY(0px);
    pointer-events: all;
    opacity: 1;
  }
}

.swiper-initialized {
  position: relative;
}

.btn {
  display: block;
  background-color: whitesmoke;
  padding: 7px 12px 8px;
  border: none;
  border-radius: 20px;
  min-width: 120px;
  height: 40px;
  text-decoration: none;
}

body,
.q-app {
  overflow: hidden auto;
}
